import React from "react";
import { SketchPicker } from "react-color";
import "../stylesheets/ColorBox.css";

/**
 * ColorBox
 *
 * @prop {String} label The name of this box
 * @prop {Color} color  The color for this box
 */
class ColorBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = { pickerActive: false };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.setState({ pickerActive: !this.state.pickerActive });
  }

  render() {
    const boxStyle = {
      backgroundColor: this.props.color,
    }

    let pickerStyle;
    if (this.state.pickerActive) {
      pickerStyle = {
        position: "absolute",
        zIndex: 100,
      }
    } else {
      pickerStyle = {
        display: "none",
      }
    }
    return (
      <div className="color-box-container">
        <div className="color-picker" style={pickerStyle}>
          <SketchPicker color={this.props.color.hex()} />
        </div>
        <div className="label">{this.props.label}</div>
        <div className="color-box" style={boxStyle} onClick={this.handleClick}></div>
        <div className="color-value">{this.props.color.hex()}</div>
      </div>
    )
  }
}

export default ColorBox;



